const ProjectsCard = ({ project }) => {
	return (
		<div className="card">
			<img src={project.img} alt={project.title} />
			<div>
				<p>
					<strong>{project.title}</strong>
				</p>
				<a href={project.link} target="_blank" rel="noreferrer">
					View live
				</a>
			</div>
		</div>
	);
};

export default ProjectsCard;
