import "./css/Header.css";
import { Fade, Zoom } from "react-reveal";
import { useEffect } from "react";

const Header = ({ getComponentHeight }) => {
	const setHeight = () => {
		setTimeout(() => {
			const myHeight = document.querySelector("#header").offsetHeight;
			getComponentHeight("home", myHeight - 100);
		}, 1000);
	};

	useEffect(() => {
		setHeight();
	}, []);

	return (
		<header id="header">
			<div className="container">
				<Fade left>
					<img src="img/my-photo.jpg" alt="my profile" />
				</Fade>
				<div>
					<Fade right>
						<p>Hi, I'm</p>
						<h2>Albert Salang</h2>
						<h3>Full-Stack Web Developer</h3>
						<p>
							I'm here to help individual and organization to address their
							problem through interactive and functional web solution.
						</p>
					</Fade>
					<Zoom>
						<a href="documents/cv.pdf" target="_blank">
							<button>Get my CV</button>
						</a>
					</Zoom>
				</div>
			</div>
		</header>
	);
};

export default Header;
