import React from "react";
import "./App.css";
import "./components/css/MediaQueries.css";
import Header from "./components/Header";
import Nav from "./components/Navigation";
import AboutMe from "./components/AboutMe";
import Skills from "./components/Skills";
import Projects from "./components/Projects";
import Contact from "./components/Contact";
import Sticky from "react-sticky-el";

class App extends React.Component {
	state = {
		componentsHeight: [
			{ name: "home", height: 0 },
			{ name: "aboutme", height: 0 },
			{ name: "skills", height: 0 },
			{ name: "projects", height: 0 },
		],
		active: "",
	};

	getComponentHeight = (name, height) => {
		let componentsHeightCopy = [...this.state.componentsHeight];
		componentsHeightCopy.forEach((component) => {
			if (component.name === name) {
				component.height = height;
				this.setState({ componentsHeight: componentsHeightCopy });
			}
		});
	};

	responsiveNav = () => {
		let componentsOffsetTop = [
			{ name: "home", offset: 0 },
			{
				name: "aboutme",
				offset:
					this.state.componentsHeight[0].height +
					this.state.componentsHeight[1].height,
			},
			{
				name: "skills",
				offset:
					this.state.componentsHeight[0].height +
					this.state.componentsHeight[1].height +
					this.state.componentsHeight[2].height,
			},
			{
				name: "projects",
				offset:
					this.state.componentsHeight[0].height +
					this.state.componentsHeight[1].height +
					this.state.componentsHeight[2].height +
					this.state.componentsHeight[3].height,
			},
		];

		if (window.pageYOffset > componentsOffsetTop[3].offset) {
			this.setState({ active: "contact" });
		} else if (
			window.pageYOffset > componentsOffsetTop[2].offset &&
			window.pageYOffset < componentsOffsetTop[3].offset
		) {
			this.setState({ active: componentsOffsetTop[3].name });
		} else if (
			window.pageYOffset > componentsOffsetTop[1].offset &&
			window.pageYOffset < componentsOffsetTop[2].offset
		) {
			this.setState({ active: componentsOffsetTop[2].name });
		} else if (
			window.pageYOffset > componentsOffsetTop[0].offset &&
			window.pageYOffset < componentsOffsetTop[1].offset
		) {
			this.setState({ active: componentsOffsetTop[1].name });
		} else if (window.pageYOffset < componentsOffsetTop[0].offset) {
			this.setState({ active: componentsOffsetTop[0].name });
		}
	};

	componentDidMount() {
		window.addEventListener("scroll", this.responsiveNav);
		setTimeout(() => {
			this.responsiveNav();
		}, 1100);
	}

	render() {
		return (
			<div id="div-app">
				<Header
					id="header-component"
					getComponentHeight={this.getComponentHeight}
				/>
				<Sticky className="nav-sticky">
					<Nav active={this.state.active} />
				</Sticky>
				<AboutMe
					id="aboutme-component"
					getComponentHeight={this.getComponentHeight}
				/>
				<Skills
					id="skills-component"
					getComponentHeight={this.getComponentHeight}
				/>
				<Projects
					id="projects-component"
					getComponentHeight={this.getComponentHeight}
				/>
				<Contact id="contact-component" />
			</div>
		);
	}
}

export default App;
