import "./css/AboutMe.css";
import Fade from "react-reveal";
import { useEffect } from "react";

const AboutMe = ({ getComponentHeight }) => {
	const setHeight = () => {
		setTimeout(() => {
			const myHeight = document.querySelector("#section-aboutme").offsetTop;
			getComponentHeight("aboutme", myHeight);
		}, 1000);
	};

	useEffect(() => {
		setHeight();
	}, []);

	return (
		<section id="section-aboutme">
			<div className="container">
				<Fade up>
					<div className="inner-container">
						<div>
							<h3>Know About Me</h3>
							<hr />
						</div>
						<div>
							<p>
								Iâ€™m Albert Salang a<span> Full-Stack Web Developer</span> living
								in Quezon City. I have finished a Bachelor Degree in Information
								Technology and currently enrolled in a coding bootcamp.
							</p>
							<p>
								Creating interactive and functional website is my{" "}
								<span>passion</span>. I enjoy challenging myself with solving
								complex problem and making code logic and structure that comes
								with it. I am also a good web and graphic designer too.
							</p>
						</div>
					</div>
				</Fade>
			</div>
		</section>
	);
};

export default AboutMe;
