import "./css/Skills.css";
import { Fade, Zoom } from "react-reveal";
import { useState, useEffect } from "react";

const AboutMe = ({ getComponentHeight }) => {
	const [frontEnd] = useState([
		{ name: "HTML", img: "img/html.png" },
		{ name: "CSS", img: "img/css.png" },
		{ name: "Javascript", img: "img/javascript.png" },
		{ name: "JQuery", img: "img/jquery.png" },
		{ name: "Bootstrap", img: "img/bootstrap.png" },
		{ name: "React", img: "img/react.png" },
		{ name: "Adobe Photoshop", img: "img/adobephotoshop.png" },
	]);

	const [backEnd] = useState([
		{ name: "Php", img: "img/php.png" },
		{ name: "Node JS", img: "img/nodejs.png" },
		{ name: "Express JS", img: "img/expressjs.png" },
		{ name: " Mysql", img: "img/mysql.png" },
		{ name: "MongoDB", img: "img/mongodb.png" },
		{ name: "Git", img: "img/git.png" },
	]);

	const setHeight = () => {
		setTimeout(() => {
			const myHeight = document.querySelector("#section-skills").offsetHeight;
			getComponentHeight("skills", myHeight);
		}, 1000);
	};

	useEffect(() => {
		setHeight();
	}, []);

	return (
		<section id="section-skills">
			<div className="container">
				<Fade>
					<div>
						<h3>Professional Skills</h3>
						<hr />
					</div>
				</Fade>
				<Zoom>
					<h4>Front End</h4>
				</Zoom>
				<div className="skills-container">
					<Zoom onReveal={setHeight}>
						{frontEnd.map((object) => (
							<div>
								<img src={object.img} alt={object.name} />
								<p>{object.name}</p>
							</div>
						))}
					</Zoom>
				</div>

				<Zoom>
					<h4>Back End</h4>
				</Zoom>
				<div className="skills-container">
					<Zoom onReveal={setHeight}>
						{backEnd.map((object) => (
							<div>
								<img src={object.img} alt={object.name} />
								<p>{object.name}</p>
							</div>
						))}
					</Zoom>
				</div>
			</div>
		</section>
	);
};

export default AboutMe;
