import "./css/Navigation.css";
import { useEffect, useState } from "react";

const Navigation = ({ active }) => {
	const [displayNav, setDisplayNav] = useState(false);

	useEffect(() => {
		const buttons = document.querySelectorAll(".nav-button");
		buttons.forEach((button) => {
			button.classList.remove("active");
		});
		if (active === "home") {
			document.querySelector("#btn-home").classList.add("active");
		} else if (active === "aboutme") {
			document.querySelector("#btn-aboutme").classList.add("active");
		} else if (active === "skills") {
			document.querySelector("#btn-skills").classList.add("active");
		} else if (active === "projects") {
			document.querySelector("#btn-projects").classList.add("active");
		} else if (active === "contact") {
			document.querySelector("#btn-contact").classList.add("active");
		}
	}, [active]);

	useEffect(() => {
		window.addEventListener("scroll", () => {
			if (displayNav === true) {
				setDisplayNav(false);
			}
		});
	}, [displayNav]);

	return (
		<nav id="nav">
			<div className="container">
				<img className="img-mylogo" src="img/my-logo.png" alt="my logo" />
				<button
					className={
						displayNav
							? "sm-display-none lg-display-none"
							: "sm-diplay-block lg-display-none"
					}
					onClick={() => {
						setDisplayNav(true);
					}}
				>
					<span class="material-icons">menu</span>
				</button>
				<button
					className={
						displayNav
							? "sm-diplay-block lg-display-none"
							: "sm-display-none lg-diplay-none"
					}
					onClick={() => {
						setDisplayNav(false);
					}}
				>
					<span class="material-icons">close</span>
				</button>
				<div
					id="nav-menu"
					className={
						displayNav
							? "sm-diplay-flex lg-display-flex"
							: "sm-display-none lg-display-flex"
					}
				>
					<a href="#header">
						<button
							id="btn-home"
							className="nav-button active"
							onClick={() => {
								setDisplayNav(false);
							}}
						>
							Home
							<hr />
						</button>
					</a>
					<a href="#section-aboutme">
						<button
							id="btn-aboutme"
							className="nav-button"
							onClick={() => {
								setDisplayNav(false);
							}}
						>
							About&nbsp;Me
							<hr />
						</button>
					</a>
					<a href="#section-skills">
						<button
							id="btn-skills"
							className="nav-button"
							onClick={() => {
								setDisplayNav(false);
							}}
						>
							Skills
							<hr />
						</button>
					</a>
					<a href="#section-projects">
						<button
							id="btn-projects"
							className="nav-button"
							onClick={() => {
								setDisplayNav(false);
							}}
						>
							Projects
							<hr />
						</button>
					</a>
					<a href="#section-contact">
						<button
							id="btn-contact"
							className="nav-button"
							onClick={() => {
								setDisplayNav(false);
							}}
						>
							Contact
							<hr />
						</button>
					</a>
				</div>
			</div>
		</nav>
	);
};

export default Navigation;
