import "./css/Projects.css";
import { Fade, Zoom } from "react-reveal";
import { useState, useCallback, useEffect } from "react";
import ProjectCard from "./ProjectsCard";
import ImageViewer from "react-simple-image-viewer";

const AboutMe = ({ getComponentHeight }) => {
	const [websites] = useState([
		{
			img: "img/static-website.png",
			title: "Responsive Static Website",
			link: "https://albertsalang.github.io/static-website/",
		},
		{
			img: "img/higher-and-lower-card-game.png",
			title: "JS Game",
			link: "https://albertsalang.github.io/lower-and-higher-cardgame/",
		},
		{
			img: "img/pokedex.png",
			title: "Pokedex",
			link: "https://albertsalang.github.io/pokedex/",
		},
		{
			img: "img/todo-app.png",
			title: "Todo App",
			link: "https://albertsalang.github.io/todo-app/",
		},
		{
			img: "img/resto-app.png",
			title: "Resto App",
			link: "https://albertsalang.github.io/resto-app/",
		},
		{
			img: "img/calculator.png",
			title: "Calculator",
			link: "https://albertsalang.github.io/calculator/",
		},
		{
			img: "img/blog-app.png",
			title: "Blog App",
			link: "https://albertsalang.github.io/blog-app/",
		},
		{
			img: "img/digital-clock.png",
			title: "Digital Clock",
			link: "https://albertsalang.github.io/digital-clock/",
		},
	]);

	const [gallery] = useState([
		"img/photo1.jpeg",
		"img/photo6.jpg",
		"img/photo2.jpeg",
		"img/photo3.jpeg",
		"img/photo4.jpeg",
		"img/photo5.jpeg",
	]);

	const [seeMore, setSeeMore] = useState(false);
	const [seeMoreGallery, setSeeMoreGallery] = useState(false);
	const [currentImage, setCurrentImage] = useState(0);
	const [isViewerOpen, setIsViewerOpen] = useState(false);

	const openImageViewer = useCallback((index) => {
		setCurrentImage(index);
		setIsViewerOpen(true);
	}, []);

	const closeImageViewer = () => {
		setCurrentImage(0);
		setIsViewerOpen(false);
	};

	const setHeight = () => {
		setTimeout(() => {
			const myHeight = document.querySelector("#section-projects").offsetHeight;
			getComponentHeight("projects", myHeight);
		}, 1000);
	};

	useEffect(() => {
		setHeight();
	}, []);

	useEffect(() => {
		isViewerOpen
			? (document.querySelector("body").style.overflow = "hidden")
			: (document.querySelector("body").style.overflow = "auto");
	}, [isViewerOpen]);

	return (
		<section id="section-projects">
			<div className="container">
				<Fade>
					<div>
						<h3>My Projects</h3>
						<hr />
					</div>
				</Fade>
				<Zoom>
					<h4>Websites</h4>
				</Zoom>
				<div id="websites-container">
					<Zoom>
						{websites.slice(0, 3).map((project) => (
							<ProjectCard project={project} />
						))}
					</Zoom>
					{seeMore
						? websites.slice(3).map((project) => (
								<Zoom onReveal={setHeight}>
									<ProjectCard project={project} />
								</Zoom>
						  ))
						: null}
				</div>
				{seeMore ? (
					<button
						onClick={() => {
							setSeeMore(false);
							setHeight();
						}}
					>
						See Less<span class="material-icons">expand_less</span>
					</button>
				) : (
					<button
						onClick={() => {
							setSeeMore(true);
							setHeight();
						}}
					>
						See More<span class="material-icons">expand_more</span>
					</button>
				)}

				<Zoom>
					<h4>Gallery</h4>
				</Zoom>
				<div id="gallery-container">
					<Zoom>
						{gallery.slice(0, 3).map((src, index) => (
							<div className="image-container">
								<img
									src={src}
									onClick={() => openImageViewer(index)}
									width="100%"
									style={{ borderRadius: "5px" }}
									key={index}
									alt={"gallery image " + index}
								/>
							</div>
						))}
					</Zoom>
					{seeMoreGallery
						? gallery.slice(3).map((src, index) => (
								<Zoom>
									<div className="image-container">
										<img
											src={src}
											onClick={() => openImageViewer(index)}
											width="100%"
											style={{ borderRadius: "5px" }}
											key={index}
											alt={"gallery image " + index}
										/>
									</div>
								</Zoom>
						  ))
						: null}
				</div>
				{seeMoreGallery ? (
					<button
						onClick={() => {
							setSeeMoreGallery(false);
							setHeight();
						}}
					>
						See Less<span class="material-icons">expand_less</span>
					</button>
				) : (
					<button
						onClick={() => {
							setSeeMoreGallery(true);
							setHeight();
						}}
					>
						See More<span class="material-icons">expand_more</span>
					</button>
				)}
			</div>
			<div className="image-viewer">
				{isViewerOpen && (
					<ImageViewer
						src={gallery}
						currentIndex={currentImage}
						onClose={closeImageViewer}
						backgroundStyle={{
							position: "fixed",
							height: "100vh",
							backgroundColor: "rgba(0,0,0,0.8)",
							zIndex: "10",
						}}
					/>
				)}
			</div>
		</section>
	);
};

export default AboutMe;
